<template>
    <div class="content-bg" v-loading.fullscreen.lock="fullscreenLoading" element-loading-text="获取中"  element-loading-background="rgba(0, 0, 0, 0.6)">
      <div class="top-title">IP主机流量</div>
      <div style="padding: 0 5px;"><v-basedate class="from-wapper" busName="IPHostFlowDateBus2"></v-basedate></div>
      <div class="item-wapper">
        <div class="item-title">源IP地址流量排序</div>
        <div class="item-content">
          <v-echarts echartType="bar" :echartData = "this.sourceIpData" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">源IP地址流量排序</div>
        <div class="item-content">
          <v-echarts echartType="pie" :echartData = "this.sourceIpData2" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">目的IP地址流量排序</div>
        <div class="item-content">
          <v-echarts echartType="bar" :echartData = "this.targetIpData" ></v-echarts>
        </div>
      </div>
      <div class="item-wapper">
        <div class="item-title">目的IP地址流量排序</div>
        <div class="item-content">
          <v-echarts echartType="pie" :echartData = "this.targetIpData2" ></v-echarts>
        </div>
      </div>
    </div>
    
</template>

<script>
    import vBasedate from '../common/baseDate'
    import vEcharts from '../common/echarts'
    import bus from '../common/bus';
    import {dateFormat} from '../../../static/js/common'
    export default {
        name: "IPHostFlow2",
        data() {
            return {
                fullscreenLoading:false,
                interTime:'',
                //刷新时间间隔
                refreshIntTime :'5000',
                //数据日期间隔
                dataTime:3600,
                //源ip地址流量排行
                sourceIpData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'源IP',
                        yAxisName:'数据包长度/KB',
                        rotate:'25',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                sourceIpData2:{
                    baseConfig:{
                        title:'',
                            hoverText:'百分比'
                    },
                    xAxisArr:[],
                        yAxisArr:[]
                },
                //目的ip地址流量排行
                targetIpData:{
                    baseConfig:{
                        title:'',
                        xAxisName:'目的IP',
                        yAxisName:'数据包长度/KB',
                        rotate:'25',
                        itemColor:['rgba(68,47,148,0.5)','rgba(15,219,243,1)']
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
                targetIpData2:{
                    baseConfig:{
                        title:'',
                        hoverText:'百分比'
                    },
                    xAxisArr:[],
                    yAxisArr:[]
                },
            }
        },
        created(){
            /*监听刷新时间改变*/
            bus.$on('IPHostFlowDateBus2',(arr)=>{
              let paramObj = {
                starttime:arr[0],
                endtime:arr[1]
              }
              this.fullscreenLoading = true;
              /*获取源IP地址流量排行*/
              this.getSourceIpData(paramObj);
              /*获取目的IP地址流量排行*/
              this.getTargetIpData(paramObj);
            })
        },
        mounted(){},
        methods:{
            /*获取源IP地址流量排行*/
            getSourceIpData(paramObj){
                this.$nextTick(()=>{
                    this.$post('/flow/getSrcIPFlow.do',paramObj)
                        .then(res=>{
                            this.fullscreenLoading = false;
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res[0]){
                                xns1.push(i);
                                yvs1.push(res[0][i]);
                                yvs2.push({
                                    name:i,
                                    value:res[0][i]
                                })
                            }
                            this.sourceIpData.xAxisArr = xns1;
                            this.sourceIpData.yAxisArr = yvs1;
                            this.sourceIpData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{
                          this.fullscreenLoading = false;
                        })
                })
            },
            /*获取目的IP地址流量排行*/
            getTargetIpData(paramObj){
                this.$nextTick(()=>{
                    this.$post('/flow/getDstIPFlow.do',paramObj)
                        .then(res=>{
                            this.fullscreenLoading = false;
                            let xns1 = [];
                            let yvs1 = [];
                            let yvs2 = [];
                            for(let i in res[0]){
                                xns1.push(i);
                                yvs1.push(res[0][i]);
                                yvs2.push({
                                    name:i,
                                    value:res[0][i]
                                })
                            }
                            this.targetIpData.xAxisArr = xns1;
                            this.targetIpData.yAxisArr = yvs1;
                            this.targetIpData2.yAxisArr = yvs2;
                        })
                        .catch(err=>{
                          this.fullscreenLoading = false;
                        })
                })
            },
        },
        components:{
          vBasedate,
          vEcharts
        }
    }
</script>

<style scoped>
  .content-bg{
    /*height: 1000px;*/
    font-size: 1rem;
    background: url("../../../static/img/flow-index-bg2.png");
    background-size: 100% 100%;
  }
  .top-title{
    font-size: 0.5rem;
    color: #185bff;
    padding: 10px;
  }
  .item-wapper{
    height: auto;
    /*background: rgb(26,36,47);*/
    background: url("../../../static/img/bd2.png");
    background-size: 100% 100%;
    margin:0.5rem 0.2rem;
  }
  .item-title{
    height: 1rem;
    line-height: 1rem;
    font-size: 0.38rem;
    text-align: left;
    padding-left: 25px;
    /*background: url("../../../static/img/title-bg.png");*/
    background-size: 100% 100%;
  }
  .item-content{
    /*height: calc(100% - 1rem);*/
    height: 230px;
    box-sizing: border-box;
    padding: 0.5rem;
    padding-top: 0;
  }
</style>



